.news-box[data-v-e029505e]{
    display: inline-block;
    margin: 20px 20px;
    width: 336px;
    background-color: #ffffff;
    position: relative;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    vertical-align: top;
}
.news-box>a[data-v-e029505e]{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
}
.news-box .pic[data-v-e029505e]{
    height: 180px;
    background-position: center center;
    background-size: 100% 100%;
    -webkit-transition: background-size 1s, opacity 1.5s;
    transition: background-size 1s, opacity 1.5s;
    opacity: 0;
}
.news-box h2[data-v-e029505e]{
    color: #333333;
    font-weight: normal;
    font-size: 18px;
    position: absolute;
    top: 200px;
    left: 20px;
}
.news-box p[data-v-e029505e]{
    color: #999999;
    font-size: 14px;
    margin: 80px 20px 20px;
    line-height: 24px;
}
.news-box .time[data-v-e029505e]{
    color: #666666;
    font-size: 14px;
    margin: 20px 20px;
}
.news-box[data-v-e029505e]:hover{
    background-color: #ffffff;
}
.news-box:hover .pic[data-v-e029505e]{
    background-size: 110% 110%;
}



